.vux-close {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  color: #999;
  width: 24px;
  height: 24px;
}
.vux-close:before,
.vux-close:after {
  content: '';
  position: absolute;
  left: 0;
  top: 11px;
  width: 24px;
  height: 1px;
  background-color: currentColor;
  transform: rotate(-45deg);
}
.vux-close:after {
  transform: rotate(45deg);
}
.bubbly-button {
  font-family: 'Helvetica', 'Arial', sans-serif;
  display: inline-block;
  font-size: 30px;
  padding: 15px 50px;
  background: linear-gradient(#ffb0a8, #d18e86);
  color: #fff;
  border-radius: 50px;
  border: none;
  position: relative;
  transition: transform ease-in 0.1s, box-shadow ease-in 0.25s;
  user-select: none;
}
.bubbly-button * {
  user-select: none;
}
@keyframes lt-star {
  0% {
    opacity: 1;
    transform: rotate(0);
  }
  10% {
    opacity: 0;
    transform: rotate(36deg);
  }
  20% {
    opacity: 1;
    transform: rotate(72deg);
  }
  30% {
    opacity: 0;
    transform: rotate(108deg);
  }
  40% {
    opacity: 1;
    transform: rotate(144deg);
  }
  50% {
    opacity: 0;
    transform: rotate(180deg);
  }
  60% {
    opacity: 1;
    transform: rotate(216deg);
  }
  70% {
    opacity: 0;
    transform: rotate(252deg);
  }
  80% {
    opacity: 1;
    transform: rotate(288deg);
  }
  90% {
    opacity: 0;
    transform: rotate(324deg);
  }
  100% {
    opacity: 1;
    transform: rotate(360deg);
  }
}
.lt-star {
  animation: lt-star 10s linear infinite;
}
.ximg-demo {
  width: 100%;
  height: auto;
  display: block;
}
@keyframes rotating {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes rotating2 {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(-360deg);
  }
}
.dazhuanpan {
  animation: rotating 10s linear infinite;
}
.dazhuanpan-yuan {
  animation: rotating2 10s linear infinite;
}
@keyframes scale {
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  to {
    transform: scale(1);
  }
}
.dazhuanpan-go {
  animation: scale 2s linear infinite;
}
.dialog-demo.video .weui-dialog {
  width: 100%;
  max-width: 100%;
  background-color: transparent;
}
.dialog-demo.video #video-1 {
  width: 100%;
  z-index: 5001;
}
.dialog-demo.yiyuan .weui-dialog {
  max-width: 100%;
  background-color: transparent;
}
.banner {
  visibility: hidden;
}
.success-img + .banner {
  visibility: visible;
}



